.about {
  margin: 30px 0px;
  padding: 20px;
  margin-left: 200px;
}

.row {
  margin-top: 21px;
}

.about-img img {
  border-radius: 10px;
  width: 100%;
  height: 400px;
}

.about-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-content h1 {
  color: grey;
  font-family: cursive;
  border-bottom: 2px solid grey;
  text-align: center;
}

.about-content p {
  text-align: justify;
  padding: 20px;
}

/* darkmode */
/* #dark .about { */
/* box-shadow: none;
  background-color: rgba(0, 0, 0, 0.15); */
/* } */

@media (max-width: 600px) {
  .about {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    width: 100%;
  }

  .about-content {
    margin-top: 10px;
  }
}
