.work {
  background-color: #1387812a;
  margin-left: 270px;
}

.work-exp {
  padding: 60px 0px;
}

.work-exp h2 {
  font-weight: bold;
  color: #1e1e2c;
}

.work hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.work-exp .vertical-timeline-element-subtitle {
  color: #1e1e2c;
  opacity: 0.5;
}

@media (max-width: 600px) {
  .work {
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }

  .work hr {
    width: 280px;
  }
}
